	module.exports = {"data":{"post":{"html":"<p>One of the pitfalls of telecommuting is that you have no idea where your co-workers are. I just now, at 11:30 am, realized that today is a holiday in Cologne, which explains why no one is sending me any mail or Jabber messages today. They may have more days off in Cologne, but the higher quality of life in Hamburg more than makes up for it.</p>\n<p>Speaking of quality of life, Hamburg just had the highest storm tide in 15 years (5.90 meters, which is actually 20 cm higher than the killer flood of 1962, if you're looking for evidence of global warming). If you parked on the Fischmarkt this morning, the inconvenient truth is that your car is now floating in the Elbe (or perhaps more inconvenient, has been impounded by the police).</p>\n<p>Update: The flood was actually a meter lower than expected, so no record. They still towed away lots of cars, though.</p>","fields":{"title":"Foreign Holidays","date":"01 Nov 2006","url":"https://papascott.de/2006-11-01-foreign-holidays/"}}},"pathContext":{"slug":"/2006-11-01-foreign-holidays/","prev":{"fields":{"title":"Expat Pitfalls","date":"31 Oct 2006","slug":"/2006-10-31-expat-pitfalls/"}},"next":{"fields":{"title":"That's Me in the Corner","date":"03 Nov 2006","slug":"/2006-11-03-thats-me-in-the-corner/"}}}}

